import { styled } from 'styletron-react';

export const InputContainer = styled('div', ({ focused, inputTextColor, styles }) => (Object.assign({
  padding: '6px 24px',
  border: '1px solid rgba(0,0,0,0.15)',
  borderColor: focused ? '#00cceb' : 'rgba(0,0,0,0.15)',
  background: 'white',
  display: 'flex',
  cursor: 'text',
  color: inputTextColor,
}, styles)));

export const StyledInput = styled('input', ({ fontFamily }) => ({
  fontFamily,
  height: '36px',
  flex: 1,
  border: 'none',
  fontSize: '1rem',
  lineHeight: '1.5',
  backgroundColor: 'transparent',
  outline: 'none',
}));



