var uppercasePattern = /[A-Z]/g;
var msPattern = /^ms-/;

module.exports = hyphenateStyleName;

function hyphenateStyleName(prop) {
  return prop
    .replace(uppercasePattern, '-$&')
    .toLowerCase()
    .replace(msPattern, '-ms-');
}



